@import "styles/variables.scss";

.content {
    margin: 60px auto 0;
    max-width: 1575px;
    padding-bottom: 340px;
}

@media screen and (max-width: 1680px) {
    .content {
        margin: 60px 40px 0 40px;
    }
}

@media #{$tablet} {
    .content {
        margin: 40px 12px 0 12px;
        padding-bottom: 520px;
    }
}