@import "styles/variables.scss";

.error500 {
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
}

.fourOhFourContainer {
    display: flex;
    flex-direction: column;
    height: 100vh;
}

.fourOhFour {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    width: 100vw;
    max-width: 100vw;
    height: 100vh;
    margin: 0;
    padding-bottom: 0;
}

.fourOhFour > h1 {
    margin-bottom: 12px;
    font-size: 80px;
    line-height: 93%;
    letter-spacing: -0.03em;
}

.fourOhFour > p {
    margin-bottom: 12px;
}

.fourOhFour > button {
    margin: 12px;
}

footer.footer {
    position: static;
}

@media #{$tablet} {
    .fourOhFour {
        margin: 0;
        min-height: 300px;
    }
    .fourOhFour > h1 {
        font-size: 56px;
    }
}