@import "styles/variables.scss";

.button {
    width: 100%;
    height: 60px;
    background-color: $blueColor;
    font-weight: 600;
    font-size: 16px;
    border: none;
    transition: 0.2s;
    & > pre {
        color: $whiteColor;
        font-weight: 500;
    }
    &:disabled {
        opacity: 0.4;
    }
}

.fullWidth {
    width: 100% !important;
}

.transBorder {
    border: 1px solid rgba(35, 35, 35, 0.3) !important;
}

.invisible {
    width: fit-content;
    padding: 10px;
    height: fit-content;
    background-color: transparent;
    border: 1px solid $darkBlue;
    color: $darkBlue;
    &:not(:disabled):hover {
        background-color: $darkBlue;
        color: $beige;
        cursor: pointer;
        & > pre {
            color: $beige;
        }
    }
}

.invisible > pre {
    color: $darkBlue;
    text-transform: uppercase;
}

.white:not(:disabled):hover {
    background-color: $beige;
}

.white:not(:disabled):hover > pre {
    color: $darkBlue;
}

.white {
    border: 1px solid $beige;
}

.white > pre {
    color: $beige;
}

.isActive {
    background-color: $darkBlue;
    height: fit-content;
    width: fit-content;
    padding: 10px;
    border: 1px solid $darkBlue;
    cursor: pointer;
}

.isActive:hover {
    background-color: $darkBlue;
}

.isActive > pre {
    color: $beige;
    text-transform: uppercase;
}

@media #{$tablet}  {
    .invisible > pre {
        font-size: 16px;
    }
}